import {http} from '../../utils/http.js'
import { Indicator } from 'mint-ui';
export default {
	data() {
		return {
			username: '',
			email: '',
			password: '',
			phone: ''
		};
		
	},
	methods:{
		login(){
			Indicator.open('Loading...');
			if(this.username && this.password){
				// const option = {
				// 	path: '/users/register',
				// 	
				// }
				// http.get()
				http.get('users/register',{
						params: {
							name: this.username,
							pwd: this.password
						}
					}).then((res)=>{
					console.log("调取注册接口后返回到数据-----》",res)
					if(res && res.data.state === 1) {
						Indicator.close();
						uni.redirectTo({
							url:'../find/find',
							success: () => {
								uni.showToast({
									title:"登陆成功"
								})
							},
							fail: () =>{
								
							}
						}) 
					}else {
						Indicator.close();
					}
				})	
			}else {
				// uni.showToast({
				// 	title:"用户名和密码为空",
				// 	duration:2000,
				// 	mask:truetrue
				// })
				Indicator.close();
				uni.showModal({
					title: '提示',
					content: '用户名密码为空',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
